<template>
  <div class="container">
   <div class="row">
     
    
    <div class="col-lg-6">
             <div class="table-responsive">
               <table class="table table-stripped table-hover table-bordered ">
                  <thead class="">
                     <tr>
                        <th colspan="2">Moedas</th>
                     </tr>    
                  </thead>
                  <tbody>
                      <tr v-for="(k, v) in currency" :key="v">
                          <td>{{v}}</td> <td>{{k}}</td> 
                      </tr>
                  </tbody>
               </table> 
              </div>  
    </div>
    <div class="col-lg-6">
             <div class="table-responsive">
               <table class="table table-stripped table-hover table-bordered ">
                  <thead class="">
                     <tr>
                        <th colspan="2">Crypto Moedas</th>
                     </tr>    
                  </thead>
                  <tbody>
                      <tr v-for="(k, v) in crypto.data" :key="v">
                         <td>{{k.symbol}}</td>   <td>{{k.name}}</td>  
                      </tr>
                  </tbody>
               </table> 
              </div>  
    </div>   
   </div>
  </div>
</template>


<script>
export default {
  name: 'AllCoins',

  data:()=>({
    currency: [],
    crypto: [],

  }),

  methods:{
    loadCurrency(config)
    {      
        axios(config)
         .then(res => {
             this.currency = res.data.rates
           }).catch(err => console.log(err))
            
    },
    loadCrypto(config)
    {      
        axios(config)
         .then(res => {
             this.crypto = res.data
           }).catch(err => console.log(err))
            
    },


  
  },
  created() {
      this.loadCurrency({url: '/currency'})
      this.loadCrypto({url: '/crypto'})
  }
}
</script>